* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    width: 100%;
    margin: 0 auto;
}

.container-login {
    width: 100%;
    min-height: 100vh;
    background: -webkit-linear-gradient(to left, #21d4fd, #b721ff);
    background: -o-linear-gradient(to left, #21d4fd, #b721ff);
    background: -moz-linear-gradient(to left, #21d4fd, #b721ff);
    background: linear-gradient(to left, #21d4fd, #b721ff);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 15px;
    box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.2);
}

.wrap-login {
    width: 400px;  
    background-color: #333;
    border-radius: 8px;
    overflow: hidden;
    padding: 77px 55px 33px 55px;
}

.login-form {
    width: 100%;
}

.login-form-tittle {
    display: block;
    font-size: 20px;
    color: white;
    line-height: 1.2;
    text-align: center;
}

.login-form-tittle img{
    width: 70px;
    margin-top: 15px;
}

.wrap-input {
    width: 100%;
    position: relative;
    border-bottom: 2px solid #adadad;
    margin: 40px auto 37px;
}

.input {
    width: 100%;
    height: 45px;
    font-size: 15px;
    color: white;
    line-height: 1.2;
    border: none;
    display: block;
    background-color: transparent;
    padding: 0 5px;
}

.focus-input {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    color: #adadad;
}

.focus-input::before{
    content: "";
    display: block;
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 2px;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
    background: -webkit-linear-gradient(to left, #b721ff, #21d4fd);
    background: -o-linear-gradient(to left, #b721ff, #21d4fd);
    background: -moz-linear-gradient(to left, #b721ff, #21d4fd);
    background: linear-gradient(to left, #b721ff, #21d4fd);
}

.focus-input::after{
    font-size: 15px;
    color: #999999;
    line-height: 1.2;    
    content: attr(data-placeholder);
    display: block;
    width: 100%;
    position: absolute;
    top: 16px;
    left: 0;
    padding-left: 5px;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
}

.input:focus {
    outline: 0;
}

.input:focus + .focus-input::after {
    top: -15px;
}

.input:focus + .focus-input::before {
    width: 100%;
}

.has-val + .focus-input::after {
    top: -15px;
}

.has-val + .focus-input::before {
    width: 100%;
}


.container-login-form-btn {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 13px;
}

.login-form-btn {
    font-size: 15px;
    border: none;
    border-radius: 8px;
    color: white;
    line-height: 1.2;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    background: -webkit-linear-gradient(to left, #b721ff, #21d4fd);
    background: -o-linear-gradient(to left, #b721ff, #21d4fd);
    background: -moz-linear-gradient(to left, #b721ff, #21d4fd);
    background: linear-gradient(to left, #b721ff, #21d4fd);
}

.login-form-btn:hover {
    cursor: pointer;
}

.text-center {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

.txt1 {
    font-size: 14px;
    color: white;
    line-height: 1.2;
    text-decoration: none;
    padding-right: 5px;
}

.txt2 {
    font-size: 14px;
    color: #21d4fd;
    line-height: 1.2;
    text-decoration: none;
}